@use 'base/variables' as v;
@use 'base/mixins' as m;
@use 'base/colores' as c;
@use 'base/tipografia' as t;


.contenedor_imagenes {
    margin-top: v.$separacion;

    @include m.tablet {
        display: grid;
        grid-template-columns: repeat(3, 1fr);
     }

    .imagen {
        margin: 1rem;
        border: 1px solid darken(c.$gris, 10%);

        -webkit-box-shadow: 4px 4px 5px 0px rgba(143,139,143,1);
        -moz-box-shadow: 4px 4px 5px 0px rgba(143,139,143,1);
        box-shadow: 4px 4px 5px 0px rgba(143,139,143,1);

            img {
                width: 100%;
                height: 20rem;
            }
    }

    h3{
        text-align: center;
        color: red;
    }
}

.contacto {
    text-align: center;
    color: c.$principal;
    padding: 1rem;

    margin: 1rem;
    border: 1px solid darken(c.$gris, 10%);

    -webkit-box-shadow: 4px 4px 5px 0px rgba(143,139,143,1);
    -moz-box-shadow: 4px 4px 5px 0px rgba(143,139,143,1);
    box-shadow: 4px 4px 5px 0px rgba(143,139,143,1);

}