
// MAVEGACION SUPERIOR

@use 'base/variables' as v;
@use 'base/mixins' as m;
@use 'base/colores' as c;
@use 'base/tipografia' as t;


.header{
    background-color: c.$principal;


    &.inicio{
        background-size: cover;
        background-position: center center;
        min-height: 30rem;
        
        @include m.tablet{
            min-height: 50rem;
        }
    }


    .barra{
        display: flex;
        flex-direction: column;
        padding-top: 2rem;
        align-items: center;

        img{
            width: 15rem;
        }

        @include m.tablet{
            flex-direction: row;
            justify-content: space-between;
            text-align: end;
        }

    }

    .navegacion{
        visibility: hidden;
        opacity: 0;
        height: 0;
        transition: visibility .2s ease-in-out, opacity 0.5s linear, height 0.5s linear;
        text-align: center;

        @include m.tablet{
            display: flex;
            align-items: right;
            opacity: 1;
            visibility: visible;
            
        }

        a{
            color: c.$blanco;
            text-decoration: none;
            font-size: 1.8rem;
            display: block;
            text-align: center;
            padding: 0.3rem;
            

            @include m.tablet{
                margin-right: 1rem;
                font-size: 2rem;
                font-weight: t.$bold;

            }
            
            
        }
        .dark_mode_boton{
            width: 2.5rem;
            margin-top: 2rem;
            margin-bottom: 2rem;
            filter: invert(100%);
            cursor: pointer;

            @include m.tablet{
                margin-top: 0;
            }
        }


    }

    .mobile_menu{
        margin-top: 2rem;

        @include m.tablet{
            display: none;
        }

        img{
            width: 2.5rem;
        }
    }

}