@use 'base/variables' as v;
@use 'base/mixins' as m;
@use 'base/colores' as c;
@use 'base/tipografia' as t;


.contenedor_termo {
    .termo1,
    .termo3 {
        margin-top: 1rem;
        
        .titulo_termo { 
            text-align: center; 
        }
        
        .superior {
            display: flex;
            justify-content: space-around;
            align-items: center;
        }
        .img_termo1,
        .img_termo3 {
            max-width: 8rem;
            max-height: 10rem;
        }

        .conte_izquierda,
        .conte_centro,
        .conte_derecha {
            border: 1px solid lighten(c.$negro, 50%);
            border-radius: 0.5rem;
            
            padding: 1rem;

            width: 33.3%;
            min-height: 16rem;
        }

        hr {
            border: 1px solid lighten(c.$negro, 70%);
            margin: 0.25rem 0 0.8rem 0;
        }

        .conte_izquierda {

            .conte_img,
            .conte_h5 {
                display: flex;
                justify-content: center;
                align-items: center;

                img {
                    max-width: 7rem;
                    max-height: 10rem;
                }
            }
            .conte_h5 {
                margin-top: 1rem;

                h5 {
                    font-size: 1.5rem;
                }
            }

            .conte_relog {
                display: flex;
                justify-content: center;
                align-items: center;
                margin-top: 1rem;
            }

        }

        .conte_centro {
            margin: 0 0.5rem;
            text-align: center;

            p {
                font-size: 1.2rem;
                margin: 0.3rem 0;
            }
            .conte_boton {
                margin-top: 0.5rem;
            }
        }

        .conte_derecha{
            text-align: center;

            .nuevo_horario_termo {
                .inicio,
                .final {
                    border: 1px solid lighten(c.$negro, 70%);
                    border-radius: 0.5rem;

                    margin: 0.25rem 0;
                    padding: 0.25rem;

                    text-align: center;
                    font-size: 1.2rem;

                    width: 7rem;
                }
            }
        }
    }

    .botones_termo {
        display: flex;
    }
}